import { createGHApp } from './module';

let affisto$ClientId = '';
let affisto$Enabled = false;

function parseUrlParams(src?: string | null): URLSearchParams {
  if (!src) return new URLSearchParams();
  try {
    const url = new URL(src);
    return url.searchParams;
  } catch (e) {
    const query = new URLSearchParams(src.split('?').slice(-1)[0] || '');
    return query;
  }
}

const sleep = async (delay: number = 500) => {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(null);
    }, delay);
  });
};

const waitForCafe24API = async (): Promise<boolean> => {
  let cnt = 0;
  while (true) {
    if (typeof CAFE24API !== 'undefined') return true;
    if (cnt > 100) return false;
    await sleep();
    cnt += 1;
  }
};

type Cafe24Member = {
  member_id: string;
  name: string;
  nick_name: string;
};
const getCafe24Member = async (): Promise<Cafe24Member> =>
  new Promise((resolve, reject) => {
    CAFE24API.getCustomerInfo(function (err: any, res: any) {
      if (err) {
        reject(err);
      } else {
        resolve(res);
      }
    });
  });

const initializeCafe24 = async (
  clientId: string
): Promise<Cafe24Member | void> => {
  const flag = await waitForCafe24API();
  if (!flag) return;
  CAFE24API.init(clientId);
  const member = await getCafe24Member();
  return member;
};

(async () => {
  const scriptSrc = document.currentScript?.getAttribute('src');
  const params = parseUrlParams(scriptSrc);
  affisto$ClientId = params.get('client') ?? '';
  affisto$Enabled = params.get('enabled') !== 'false';
  const member = await initializeCafe24(affisto$ClientId);
  const memberId = member?.member_id;

  window['affisto.widget'] = createGHApp(
    affisto$ClientId,
    memberId,
    affisto$Enabled
  );
  window['affisto.widget'].show();
})();
