export function createFAB(clientId: string, refId?: string) {
  const page = _createPage(clientId, refId);
  const style = document.createElement('style');
  style.innerHTML = `
    
  `;
  document.head.appendChild(style);
  document.body.appendChild(page);
  const btn = _createButton(() => {
    page.style.visibility = 'unset';
    page.style.opacity = 'unset';
  });
  document.body.appendChild(btn);
  _createTooltip(btn, '', '');
}

const SAMPLE_IMG =
  'data:image/png;base64,';

function _createButton(_showChatPopup: any) {
  const button = document.createElement('div');
  button.style.cssText = `
    width: 48px;
    height: 48px;
    overflow: hidden;
    position: fixed;
    right: 8px;
    bottom: 8px;
    background: white;
    border: solid gray 1px;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
  `;
  const img = document.createElement('img');
  img.src = SAMPLE_IMG;
  img.style.cssText = `
    width: 44px;
    height: 44px;
    border-raidus: 50%;
    overflow: hidden;
  `;
  button.appendChild(img);
  button.addEventListener(
    'mouseenter',
    () => (button.style.boxShadow = '0px 4px 15px rgba(0, 0, 0, 0.4)')
  );
  button.addEventListener(
    'mouseleave',
    () => (button.style.boxShadow = '0px 2px 10px rgba(0, 0, 0, 0.2)')
  );
  button.addEventListener('click', _showChatPopup);
  return button;
}

function _createTooltip(
  button: HTMLDivElement,
  text: string,
  showTooltip: string
) {
  const tooltip = document.createElement('div');
  tooltip.innerText = text;
  tooltip.style.cssText = `
    position: absolute;
    bottom: 80px;
    right: 10px;
    background-color: rgba(0, 0, 0, 0.7);
    color: #fff;
    padding: 5px 10px;
    border-radius: 4px;
    display: ${showTooltip === 'hover' ? 'none' : 'block'};
  `;
  if (showTooltip === 'hover') {
    button.addEventListener(
      'mouseenter',
      () => (tooltip.style.display = 'block')
    );
    button.addEventListener(
      'mouseleave',
      () => (tooltip.style.display = 'none')
    );
  }
  return tooltip;
}

function _createPage(clientId: string, refId?: string) {
  const page = document.createElement('div');
  page.style.cssText = `
    position: fixed;
    z-index: 2147483647;
    height: min(650px, 90%);
    width: min(350px, 90%);
    margin: 0;
    border-radius: 12px;
    box-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.2);
    right: 16px;
    bottom: 16px;
    overflow: hidden;
    transition: all 0.1s ease-in;
    visibility: hidden;
    opacity: 0;
  `;
  const container = document.createElement('div');
  container.style.cssText = `
    position: relative;
    height: 100%;
  `;
  page.appendChild(container);
  const iframe = document.createElement('iframe');
  iframe.allowFullscreen = true;
  iframe.allow = 'clipboard-read; clipboard-write';
  const url = new URL('https://shop-widget.vercel.app');
  // const url = new URL('http://localhost:3001');
  url.pathname = `/w/${clientId}`;
  url.searchParams.set('host', encodeURIComponent(location.origin));
  url.searchParams.set('cpid', clientId);
  if (refId) url.searchParams.set('refid', refId);
  iframe.src = url.toString();
  iframe.style.cssText = `
    height: 100%;
    border: none;
    overflow-y: scroll;
    width: 100% !important;
  `;
  container.appendChild(iframe);
  const closeBtn = document.createElement('div');
  closeBtn.style.cssText = `
    position: absolute;
    top: 16px;
    right: 16px;
    cursor: pointer;
  `;
  const img = document.createElement('img');
  img.src = 'https://s3.incento.kr/assets/script/icon-close-black.png';
  img.style.cssText = `
    width: 16px;
    height: 16px;
  `;
  img.onclick = () => {
    page.style.visibility = 'hidden';
    page.style.opacity = '0';
  };
  closeBtn.appendChild(img);
  container.appendChild(closeBtn);
  return page;
}
